import Swiper from './Swiper';

var bannerUrl = require('../../assets/images/home/banner1.png');

export default {
  name: 'Home',
  components: {
    Swiper: Swiper
  },
  created: function created() {
    this.businessList.forEach(function (item, i) {
      var currentUrl = require("../../assets/images/home/business".concat(i + 1, ".png"));

      item.url = currentUrl;
    });
  },
  methods: {
    linkTo: function linkTo(url, params) {
      this.$router.push({
        path: url,
        query: {
          id: params
        }
      });
    }
  },
  data: function data() {
    return {
      bannerUrl: bannerUrl,
      imgList: [bannerUrl],
      businessList: [{
        name: '财富管理',
        address: '/Business'
      }, {
        name: '社区金融',
        address: '/Business'
      }, {
        name: '基金销售',
        address: '/Business'
      }, {
        name: '区块链金融',
        address: '/Business'
      }]
    };
  }
};