import "@/assets/images/iconfont/iconfont.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
var vm = null;
export default {
  name: "Notice",
  data: function data() {
    return {
      datas: [{
        title: "公告：《致客户的一封信》",
        url: "/News/News_Notice_info5"
      }, {
        title: "公告：《2020春节客服服务时间公告》",
        url: "/News/News_Notice_info6"
      }],
      swiperOption: {
        // notNextTick: true,
        direction: "vertical",
        //控制滚动的方向
        speed: 1200,
        height: 40,
        // paginationClickable: true,
        autoplay: {
          delay: 1500 //这里需要注意，如果想每2秒去自动切换，直接autoplay:2000是失效的，

        },
        loop: true,
        on: {
          click: function click(e) {
            //注意点：想要给滚动的列表的每一项加上点击事件，需要在这个回调里面进行，否则不起作用
            console.log(this);
            vm.handleClickSlide(this);
          }
        }
      }
    };
  },
  components: {
    swiper: swiper,
    swiperSlide: swiperSlide
  },
  created: function created() {
    vm = this;
  },
  computed: {
    mySwiper: function mySwiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  methods: {
    stopPlay: function stopPlay() {
      console.log("stop");
      this.mySwiper.autoplay.stop();
    },
    play: function play() {
      this.mySwiper.autoplay.start();
      console.log("play");
    },
    toDetail: function toDetail(item) {
      console.log(item);
      this.$router.push(item.url);
    },
    handleClickSlide: function handleClickSlide(vm) {
      var index = vm.clickedIndex - vm.activeIndex + vm.realIndex === 7 ? 0 : vm.clickedIndex - vm.activeIndex + vm.realIndex; // 我得到的index就是点击的item在实际数组中的下标index

      console.log(index); // 后面可以根据得到的index判断跳到每个对应的路由this.$router.push({path: `/single`});

      if (index == 0) {
        this.$router.push("/News/News_Notice_info5");
      } else if (index == 1) {
        this.$router.push("/News/News_Notice_info6");
      }
    }
  }
};