import "@/assets/images/iconfont/iconfont.css";
export default {
  name: 'HDNav',
  data: function data() {
    return {
      // 当前主导导航
      currentNav: 1,
      // 主导航的链接
      linkList: ['', 'Business', 'News', 'Application', 'About', 'Help_Center'],
      // 新闻导航的链接
      // newsLinkList: ['/News','/News/News_Research','/News/News_Notice','/News/News_School'],
      newsLinkList: ['/News', '/News/News_Research', '/News/News_School'],
      // 当前新闻导航
      currentNewsNav: 0
    };
  },
  mounted: function mounted() {
    this.getCurrentRoute();
  },
  watch: {
    $route: function $route(to, from) {
      this.getCurrentRoute();
    }
  },
  methods: {
    toHome: function toHome() {
      this.$router.push('/');
    },
    linkTo: function linkTo(i) {
      this.currentNav = i;
      var link = this.linkList[i - 1];
      var current = this.$route.path;
      var temLink = "/" + link.toString();

      if (current == temLink) {
        //点击同一个路由直接return
        return;
      }

      this.$router.push("/".concat(link));
    },
    newsLinkTo: function newsLinkTo(i) {
      this.currentNewsNav = i;
      var route = this.newsLinkList[i];
      var current = this.$route.path;

      if (route == current) {
        //点击同一个路由直接return
        return;
      }

      this.$router.push(route);
    },
    getCurrentRoute: function getCurrentRoute() {
      var _this = this;

      var current = this.$route.path;
      var len = this.linkList.length;

      for (var i = len - 1; i >= 0; i--) {
        var cur = this.linkList[i];

        if (current.indexOf(cur) > -1) {
          this.currentNav = i + 1;

          if (this.currentNav == 3) {
            this.newsLinkList.forEach(function (item, j) {
              if (item == current) {
                _this.currentNewsNav = j;
              }
            }); // 注意由于后来的公司动态和平台公告合并到一起，为了解决当点击公告轮播中的某一公告时，咨询动态的列表未被选中的问题

            if (current.indexOf('/News/News_Notice_info') > -1) {
              this.currentNewsNav = 0;
            }
          }

          break;
        }
      }
    }
  }
};